import React from 'react'
import utilisateurImg from '../images/utilisateur.png'
import './help.css'

function gestionCompte() {
  return (
    <>
        <div className='gestion-compte-container'>
            <div>
                <div className='img-gesCompte-container'><img src={utilisateurImg} alt='' />
                    <h2>Gestion des Comptes</h2>
                </div>
                <h3>Comment crer un compte sur DzEstates ?</h3>
                <p>
                    1. Cliquez sur S'inscrire' en haut  droite de la page d'accueil du site.<br></br>
                    2. Renseignez votre Nom et prenom , (Notez bien qu'ils seront au tant
                    que votre nom d'utilisateur sur le site) .<br></br>
                    3. Renseignez votre adresse email. Attention, un E-mail de validation va vous tre 
                    envoy  cette adresse, vrifiez donc sa conformit.<br></br>
                    4. Dfinissez votre mot de passe.<br></br>
                    5. Confirmez votre mot de passe.<br></br>
                    6. Validez les informations saisis .
                    7. La cration de votre compte est finalise !<br></br>
                </p>
            </div>
            <div>
                <h3>Qu'est je dois faire si j'oublie le mot de passe de mon compte ?</h3>
                <p>
                    1. Cliquez sur Se connecter en haut  droite de la page d'accueil du site.<br></br>
                    2. Cliquez sur mot de passe oubli .<br></br>
                    3. Renseignez votre adresse email. Attention, un E-mail de vrification va vous tre 
                    envoy  cette adresse, vrifiez donc sa conformit.<br></br>
                    4. Cliquez sur lien compris dans l' E-mail envoy.<br></br>
                    5. rinitialiser votre mot de passe.<br></br>
                    6. Confirmez le mot de passe .
                    6. La rinitialisation de votre mot de passe est finalise ! Vous pous maitenant 
                    connecter  votre compte de nouveau .<br></br>
                </p>
            </div>
        </div>
    </>
  )
}


export default gestionCompte
