import React from "react";
import AdsImage from "../images/image00.png"

function GestionAds(){
    return(
        <>
            <div className="gestionAds-Help-container">
            <div className='img-gesAds-container'><img src={AdsImage} alt='' />
                    <h2>Gestion des Annonces</h2>
                </div>
                <h3>Comment dposer une annonce ?</h3>
                <ul>
                    <li>accdez au dpt d'annonce en cliquant sur "Dposer une annonce"<br></br>
                        <span>Note:</span>pour dposer une annonce vous devez tre connect ou bien vous crer un compte DzEstates.
                    </li>
                    <li>renseignez les informations principales de l'annonce en remplissant les champs requis</li>
                    <li>ajoutez des photos(Vous pouvez ajouter jusqu' 3 photos gratuitement sur l'ensemble des catgories, 
                        et jusqu' 10 photos gratuitement dans les catgories dcoration, linge de maison, art de la table, 
                        chaussures, vtements, vtements bb et les familles loisirs et multimdia.)</li>
                    <li>finalisez et publiez l'annonce en cliquant sur "dposer"</li>
                </ul>
                <h3>Comment rechercher une annonce ?</h3>
                <ul>
                    <li>Rendez-vous sur le moteur de recherche de la page d'accueil</li>
                    <li>Slectionner la catgorie du bien, service ou emploi que vous recherchez.</li>
                    <li>Affiner votre recherche en renseignant les filtres associs  la catgorie.</li>
                    <li>Renseignez un ou plusieurs mots-cls dans le moteur de recherche.</li>
                    <li>Saisissez une localisation. Vous pouvez recherchez dans Toute la France ou additionner
                    jusqu' 10 localisations diffrentes (des rgions et les rgions voisines, des dpartements 
                    et les dpartements voisins, des villes et des codes postaux).  tout moment vous pouvez 
                    supprimer les localisations qui ne sont pas adaptes  votre nouvelle recherche, ou en ajouter. 
                    En saisissant une localisation, vous pourrez retrouver les localisations de vos trois dernires 
                    recherches.</li>
                </ul>
            </div>
        </>
    ) ;
}
export default GestionAds ;